const SectionHeader = (props) => {
    return (
        <div className="l-row">
            <div className="l-col l-col-12">
                <h4 className="opl-section__heading u-no-margin">{props.label}</h4>
                <Trigger>
                    <span className="opl-section__text-show">{'rozwiń sekcję "'+props.label+'"'}</span>
                    <span className="opl-section__text-hide">{'zwiń sekcję "'+props.label+'"'}</span>
                </Trigger>
            </div>
        </div>
    );
};

export const TitledFoldableTvVasesProductGroup = (props) => {
    const {section, products, onClick, onClickWrapped, net} = props;
    const {inputType, sectionTitle} = section;
    if (products && products.length > 0 && section.innerLists && section.innerLists.length > 0) {
        return (
            <Section
                header={(<SectionHeader label={sectionTitle}/>)}
                variant="section"
                headerClassName="opl-section__header--vases-tv"
                expanded={props.isExpanded}
                key={sectionTitle + props.isExpanded}
            >
                <Expander id={`exp-${props.uid}`} scrollToSelected={false} variant="" className="opl-expander-inner u-no-padding" sectionClassName="opl-section-outer--2">
                    {
                        section.innerLists.map((section, idx) => {
                            const sectionProducts = products.filter(p => p.productType === section.productFilter);
                            const innerInputType = section.inputType || inputType;
                            if (sectionProducts.length > 0) {
                                return [
                                    <h4 key={idx + 'h4'} className="u-no-margin u-padding-l u-padding-top-l u-border-bottom u-small-no-border u-small-no-padding-top">{section.sectionTitle}</h4>,
                                    <InnerTvVasesProductList
                                        key={idx + 'innerProduct'}
                                        products={sectionProducts}
                                        onClick={onClick}
                                        inputType={innerInputType}
                                        onClickWrapped={onClickWrapped}
                                        net={net}
                                        configurables={props.configurables}
                                        descriptions={props.descriptions}
                                    />
                                ]
                            }
                        })
                    }
                </Expander>
            </Section>)
    } else {
        return null;
    }
};

