export const ProductGroup = (props) => {
    return (
        <Expander scrollToSelected={false} variant="" className="opl-expander-outer u-no-padding" sectionClassName="opl-section-outer">
            <InnerProductList products={props.products} onClick={props.onClick} onClickWrapped={props.onClickWrapped} inputType={props.inputType} net={props.net} configurables={props.configurables}/>
        </Expander>
    );
};

const InnerTvVasesProductList = (props) => {

    function selectInputTypeFrom(vasProduct){
        if(!!vasProduct.quantityGroup){
            return 'QUANTITY';
        }
        return vasProduct.inputType ? vasProduct.inputType : props.inputType;
    }

    function isProductRelatedToMultiroom(vasProduct){
        return vasProduct.migrated && !!vasProduct.quantityGroup;
    }

    function createAnotherBoxOnlyForMigratedMultiroomProducts(vasProduct, index) {
        if(isProductRelatedToMultiroom(vasProduct)){
            return Optional.of(getImmutableMigratedBox(props, {...vasProduct, visible: true, selected: true, id: vasProduct.id+"_BEFORE_MIGRATION"}, index));
        }
        return Optional.of(null);
    }

    function renderSortedBoxes(){
        let quantityGroups = [];
        let result = [];
        let productsLength = props.products.length;
        let migratedMultiroomBoxes = [];
        let insertMigratedIndex = productsLength;
        let resultLength = result.length;
        props.products.forEach((v, idx) => {
            if (!v.quantityGroup || !_.includes(quantityGroups, v.quantityGroup)) {
                let vas = {...v};
                if (!!vas.quantityGroup) {
                    quantityGroups.push(vas.quantityGroup);
                    vas.groupedProducts = [...props.products.filter(v => vas.quantityGroup === v.quantityGroup)];
                    insertMigratedIndex = resultLength+1;
                }
                resultLength = result.push(getBox(props, vas, selectInputTypeFrom(vas), idx));
                createAnotherBoxOnlyForMigratedMultiroomProducts(vas, productsLength+idx).ifPresent((migratedBox) => migratedMultiroomBoxes.push(migratedBox));
            }
        });
        migratedMultiroomBoxes.forEach( box => {
            result.splice(insertMigratedIndex, 0, box);
            insertMigratedIndex++;
        });
        return result;
    }

    if(props.products && props.products.length > 0){

        return (
            <ul className="u-margin__ul-border-collapse">
                {renderSortedBoxes()}
            </ul>
        )
    } else {
        return null;
    }
};

const InnerProductList = (props) => {
    if(props.products && props.products.length > 0){
        let quantityGroups = [];
        let result = [];

        if (props.migratedProducts && props.migratedProducts.length > 0 ) {
              result.push(getMigratedBox(props, props.migratedProducts));
        }

        props.products.forEach((v, idx) => {
            if (!v.quantityGroup || !_.includes(quantityGroups, v.quantityGroup)) {
                let vas = {...v};
                let inputType = vas.inputType ? vas.inputType : props.inputType;
                if (!!vas.quantityGroup) {
                    quantityGroups.push(vas.quantityGroup);
                    inputType = 'QUANTITY';
                    vas.groupedProducts = [...props.products.filter(v => vas.quantityGroup === v.quantityGroup)];
                }
                result.push(getBox(props, vas, inputType, idx));
            }
        });
        return (<div>
            <ul className="u-margin__ul-border-collapse">
                {result}
            </ul>
            </div>
        )
    } else {
        return null;
    }
};

const getImmutableMigratedBox = (props, vas, idx) => {
    return (
        <DisableWrapper hidden={vas.selected !== true || !vas.visible} key={"WRAPPER" + vas.id}>
            <FixProductImmutableMigratedBox key={vas.id} vas={vas} idx={idx}
                                    onSelectionChanged={() => {}} net={props.net} descriptions={props.descriptions}/>
        </DisableWrapper>
    );
};

const getMigratedBox = (props, migratedProducts) => {
            return (
                    <FixProductMigratedFirstCheckBox products={migratedProducts}  onSelectionChanged={props.onClick}
                                                     onClickMigratedCheckBox={props.onClickMigratedCheckBox} net={props.net} borderDescription={props.migratedServices.borderDescription}/>
            );
};

const getBox = (props, vas, inputType, idx) => {
    switch(inputType) {
        case "MIGRATED": {
            return (
                <DisableWrapper hidden={vas.selected !== true || !vas.visible} key={"WRAPPER" + vas.id + idx}>
                    <FixProductMigratedBox key={ vas.id } vas={ vas } idx={ idx } onSelectionChanged={ props.onClick(vas.id, vas.propositionId) } />
                </DisableWrapper>
            );
        }
        case "CHECKBOX": {
            return (
                <DisableWrapper hidden={vas.presentation === "hidden"} key={"WRAPPER" + vas.id + idx}>
                    <FixProductCheckBox key={ vas.id } vas={ vas } idx={ idx } onSelectionChanged={ props.onClick }  onClickWrapped={props.onClickWrapped} net={props.net}/>
                </DisableWrapper>
            );
        }
        case "RADIO": {
            return (
                <DisableWrapper hidden={vas.presentation === "hidden"} key={"WRAPPER" + vas.id + idx}>
                    <FixProductRadioBox key={ vas.id } vas={ vas } idx={ idx } onSelectionChanged={ props.onClick(vas.id, vas.propositionId) } net={props.net}/>
                </DisableWrapper>
            );
        }
        case "UNCHANGEABLE": {
            return (
                <DisableWrapper hidden={vas.presentation === "hidden"} key={"WRAPPER" + vas.id + idx}>
                    <FixProductImmutableBox key={vas.id} vas={vas} idx={idx} channels={props.channels}
                                            tvPackages={props.tvPackages}

                                            showPrice={false} selected={true} net={props.net}
                                            includedInThePackageTitle={props.includedInThePackageTitle ? props.includedInThePackageTitle : ""}/>
                </DisableWrapper>
            );
        }
        case "READONLY": {
            return (
                <DisableWrapper hidden={vas.presentation === "hidden"} key={"WRAPPER" + vas.id + idx}>
                    <FixProductImmutableBox key={vas.id} vas={vas} idx={idx}
                                            tvPackages={props.tvPackages}
                                            onSelectionChanged={() => {}}
                                            showPrice={true} selected={false} net={props.net}
                                            includedInThePackageTitle={props.includedInThePackageTitle ? props.includedInThePackageTitle : ""}/>
                </DisableWrapper>
            );
        }
        case "DISABLED": {
            return (
                <DisableWrapper disable={true} hidden={vas.presentation === "hidden"} key={"WRAPPER" + vas.id + idx}>
                    <FixProductCheckBox key={ vas.id } vas={ vas } idx={ idx } onSelectionChanged={ props.onClick }  onClickWrapped={props.onClickWrapped} net={props.net}/>
                </DisableWrapper>
            );
        }
        case "QUANTITY": {
            return (
                <DisableWrapper hidden={vas.groupedProducts.every(v => v.presentation === "hidden")} key={"WRAPPER" + vas.id + idx}>
                    <FixProductQuantityBox key={ vas.id } vas={ vas } idx={ idx } onSelectionChanged={ props.onClick }  onClickWrapped={props.onClickWrapped} net={props.net} configurables={props.configurables}/>
                </DisableWrapper>
            )
        }
        default: {
            return (
                <DisableWrapper hidden={vas.presentation === "hidden"} key={"WRAPPER" + vas.id + idx}>
                    <FixProductCheckBox key={ vas.id } vas={ vas } idx={ idx } onSelectionChanged={ props.onClick } net={props.net}/>
                </DisableWrapper>
            );
        }
    }
};

const DisableWrapper = (props) => {
    if(props.hidden){
        return <div className="u-hide"/>
    }
    if(props.disable){
        return <div style={{opacity: "0.5", pointerEvents: "none"}}>{props.children}</div>;
    }
    return <div>{props.children}</div>
}

